import { useEffect, useState } from "react";
import { post } from "../helper/util";
import  swal from 'sweetalert'

function Referrals(){
    let [users, setUsers] = useState([])
    let [openNewReferralModal,setOpenNewReferralModal] = useState(false)
    let [openReferralModal,setReferralModal] = useState(false)
    let [mobile,setMobile] = useState(false)
    let [referrals, setReferrals] = useState([])
    let [packagesList, setPackagesList] = useState([]);

    let [userPackage, setUserPackage] = useState("");

    let [paymentMethod, setpaymentMethod] = useState('CASH_IN_HAND')
    let [cryptoCoin, setCryptoCoin] = useState('')

    let load = async ()=>{
        let response = await post('referral/get_referral');
        setUsers(response.data.arrList)

        response = await post('common/get_drop_down',{type:"package"},{Authorization:''});
        setPackagesList(response.data.arrList)
    }

    let loadReferrals = async(event)=>{
        let response = await post('referral/get_referral',{id:event.target.dataset.id});
        console.log(referrals);
        setReferrals(response.data.arrList)
        setReferralModal(true)
    }

    let modalPopUp = (modal,open)=>{
        if(modal === 'newReferrals')
            setOpenNewReferralModal(open)
        else
            setReferralModal(open)
    }

    let createReferral = async()=>{
        await post('referral/create_referral',{strMobile:mobile,strPayMethode:paymentMethod,strCryptoCoin:cryptoCoin,fkPackageId:userPackage}).then(()=>{
            setOpenNewReferralModal(false)
            swal("Success!", "New Referral Created");
            setMobile("")
            load();
        });
    }

    useEffect(()=>{
        load();
    },[])

    return(
        <div className="news">
        <div style={{textAlign:'right'}}>
         <button className="period" onClick={modalPopUp.bind(this,'newReferrals',true)}>New Referral</button>
        </div>
          <div className="referral">
            <div className="referral-top">
              <p className="referral-item-head">Name</p>
              <p className="referral-item-head">MobileNumber</p>
              <p className="referral-item-head">Reward Amount</p>
              <p className="referral-item-head">Referral Code</p>
              <p className="referral-item-head">Status</p>
              <p className="referral-item-head">Created Time</p>
              <p className="referral-item-head">Action</p>
            </div>
            {users.map((user,index)=>{
                return(
                    <div className="referral-sub" key={index}>
                        <p className="referral-item">{user.strUser}</p>
                        <p className="referral-item">{user.strMobile}</p>
                        <p className="referral-item">{user.intRewardAmount}</p>
                        <p className="referral-item">{user.strReferralCode}</p>
                        <p className="referral-item">{user.strReferralStatus}</p>
                        <p className="referral-item">{user.createdTime}</p>
                        <p className="referral-item" style={{textAlign:'right'}}>
                            <button className="period" onClick={loadReferrals} data-id={user.id}>Referrals</button>
                        </p>
                  </div>
                )
            })}
           
          </div>
        <div data-ml-modal className={openNewReferralModal?'show':''}>
	        <span  onClick={modalPopUp.bind(this,'newReferrals',false)} className={openNewReferralModal?'modal-overlay show':'modal-overlay'}></span>
            <div class="modal-dialog modal-dialog-sm">
                <span href="" onClick={modalPopUp.bind(this,'newReferrals',false)} className="modal-close">&times;</span>
                <h5>New Referrals</h5>
                <div class="modal-content">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="form-group">
                                <input type="text" className="form-control mobile-text" placeholder="Mobile Number"  onChange={(e)=>mobile = e.target.value} />
                            </div>
                            <div className="form-group">
                                <select onChange={e => setUserPackage(e.target.value)} className="form-control payment-mode" style={{width:'100%',  margin: '0px', padding: '10px 4px',}}>
                                    {
                                        packagesList.map((item,index)=>{
                                            return <option value={item.id} key={index}>{item.strName}</option>
                                        })
                                    }
                                </select>
                            </div>
                            <div className="form-group" value={paymentMethod} onChange={e=>(setpaymentMethod(e.target.value),setCryptoCoin(""))}>
                                    <select className="form-control payment-mode">
                                        <option value="CASH_IN_HAND">Cash in Hand</option>
                                        <option value="UPI">UPI</option>
                                        <option value="CARD">Card</option>
                                        <option value="CRYPTO">Crypto</option>
                                    </select>
                                </div>
                            <div className="form-group">
                            {
                                    (paymentMethod === 'CRYPTO') &&(
                                        <div className="form-group" value={paymentMethod} onChange={e=>(setCryptoCoin(e.target.value))}>
                                            <select className="form-control payment-mode">
                                                <option value="Bitcoin">Bitcoin BTC</option>
                                                <option value="Ethereum">Ethereum ETH</option>
                                                <option value="Dogecoin">Dogecoin DOGE</option>
                                                <option value="SHIBA">SHIBA SHIB</option>
                                                <option value="Tether">Tether USDT</option>
                                                <option value="TRON">TRON TRX</option>
                                            </select>
                                        </div>
                                        )
                                }
                            </div>
                            <div className="form-group" style={{textAlign: 'center', paddingTop: '17px'}}>
                            <button className="period"  onClick={createReferral}>Create Referral</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div data-ml-modal className={openReferralModal?'show':''}>
	        <span  onClick={modalPopUp.bind(this,'referrals',false)} className={openReferralModal?'modal-overlay show':'modal-overlay'}></span>
            <div class="modal-dialog modal-dialog-lg">
                <span href="" onClick={modalPopUp.bind(this,'referrals',false)} className="modal-close">&times;</span>
                <h5>Referrals List</h5>
                <div class="modal-content">
                <table style={{width:'100%'}} border="1">
                    
                    {
                        referrals.map((item,index)=>{
                            return (
                                <tr className="" style={{border: '1px solid'}} key={index}>
                                    <td style={{border: '1px solid',padding: '6px'}}>{item.strUser}</td>
                                    <td style={{border: '1px solid',padding: '6px'}}>{item.strMobile}</td>
                                    <td style={{border: '1px solid',padding: '6px'}}>{item.intRewardAmount}</td>
                                    <td style={{border: '1px solid',padding: '6px'}}>{item.strReferralCode}</td>
                                    <td style={{border: '1px solid',padding: '6px'}}>{item.createdTime}</td>
                                </tr>
                            )
                        })
                    }
                </table>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Referrals;