import { useState,useEffect } from "react";
import swal from "sweetalert";
import { post } from "../helper/util";


function Account() {
    let [totalAmont, setTotalAmont] = useState(0)
    let [debitAmount, setDebitAmount] = useState(0)
    let [roe, setRoe] = useState(0)
    let [openAddAmountModal, setOpenAddAmountModal] = useState(0)
    let [openWithdrawAmountModal, setWithdrawAddAmountModal] = useState(0)

    let [accountAddAmount, setAccountAddAmount] = useState(0)
    let [accountAddAmountINR, setAccountAddAmountINR] = useState(0)
    let [accountwithdrawAmount, setAccountWithdrawAmount] = useState(0)
    let [paymentMethod, setpaymentMethod] = useState('CASH_IN_HAND')
    let [cryptoCoin, setCryptoCoin] = useState('')

    // let [creditAmount, setCreditAmount] = useState(0)
    let [balanceHostory, setBalanceHostory] = useState([]);

    async function withdrawAmount(){
        let response = await post('returns/create_return',{type:'WALLET',intDebitAmount:accountwithdrawAmount},{},'http://65.0.249.156:3002')
        swal('Amount Debited',response.data.strMessage).then(e=>(setAccountWithdrawAmount(0),setWithdrawAddAmountModal(false)))
        load();
    }

    async function addAmount() {
        let response = await post('wallet/create_wallet',{intCreditAmount:accountAddAmount,intBaseCreditAmont:0,strPayMethode:paymentMethod,strCryptoCoin:cryptoCoin},{},'http://65.0.249.156:3002')
        swal('Amount credited',response.data.strMessage).then(e=>( setAccountAddAmount(0),setOpenAddAmountModal(false)))
        load();
    }

    let modalPopUp = (modal,open)=>{
        if(modal === 'credit')
            setOpenAddAmountModal(open)
        else
            setWithdrawAddAmountModal(open)
    }


    let load = async()=>{
        let response = await post('/wallet/get_wallet',{},{},'http://65.0.249.156:3002')
        setTotalAmont(response.data.intTotalAmont)
        setDebitAmount(response.data.intDebitAmount)
        setBalanceHostory(response.data.arrList)


        response = await post('common/get_drop_down',{"type":"roe"})
        setRoe(response.data.roe)
    }

    useEffect(()=>{
        
        load()

    },[])

    return(
        <div>
            <div className="account-over-all-report">
                <div className="content-top">
                    <div className="content-card">
                        <div className="content-card-top">
                            <p className="content-card-head">Account Balance</p>
                            <p className="content-card-data"><button className="period" onClick={modalPopUp.bind(this,'credit',true)} >Add Amount</button></p>
                        </div>
                        <div className="content-card-btm content-card-top">
                            <p className="content-card-amount">${totalAmont}</p>
                            <img src="/static/media/increacse2.d2254016.svg" alt="" className="content-card-graph"/>
                        </div>
                    </div>
                    <div className="content-card">
                        <div className="content-card-top">
                            <p className="content-card-head">Withdraw Balance</p>
                            <p className="content-card-data"><button className="period" onClick={modalPopUp.bind(this,'withdraw',true)} >Withdraw Amount</button></p>
                        </div>
                        <div className="content-card-btm content-card-top">
                            <p className="content-card-amount">${debitAmount}</p>
                            <img src="/static/media/increacse2.d2254016.svg" alt="" className="content-card-graph"/>
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <div className="table-div">
                    <span className="report-table-title">Account Balance History</span>
                    <table className="report-table">
                        <thead>
                            <tr>
                                <th>Sl No</th>
                                <th>Amount</th>
                                <th>Payment Method</th>
                                <th>Description</th>
                                <th>DocID</th>
                                <th>Time</th>
                                
                            </tr>
                        </thead>
                        <tbody>
                        {balanceHostory.map((item,index)=>{
                            return(
                                <tr key={index} className={item.intCreditAmount !=0 ? 'credit-row':'debit-row'}>
                                    <td>{index+1}</td>
                                    <td class="amount">{item.intCreditAmount !=0 ? item.intCreditAmount:item.intDebitAmount} ({item.intBaseCreditAmount !==0 ? item.intBaseCreditAmount:item.intBaseDebitAmount})</td>
                                    <td>{item.strPayMode}</td>
                                    <td>{item.strText}</td>
                                    <td>{item.strDocId}</td>
                                    <td>{item.createdTime}</td>
                                </tr>
                            )
                        })}
                        </tbody>
                        
                    </table>
                </div>
            </div>

            <div data-ml-modal className={openWithdrawAmountModal?'show':''}>
                <span  onClick={modalPopUp.bind(this,'withdraw',false)} className={openWithdrawAmountModal?'modal-overlay show':'modal-overlay'}></span>
                <div className="modal-dialog modal-dialog-sm">
                    <span href="" onClick={modalPopUp.bind(this,'withdraw',false)} className="modal-close">&times;</span>
                    <h5>Withdraw</h5>
                    <div className="modal-content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group">
                                    <input type="number" className="form-control mobile-text" placeholder="Amount"  value={accountwithdrawAmount} onChange={e=>(setAccountWithdrawAmount(e.target.value))} />
                                </div>
                                <div className="form-group" style={{textAlign: 'center', paddingTop: '17px'}}>
                                    <button className="period" onClick={withdrawAmount}>Debit Amount</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div data-ml-modal className={openAddAmountModal?'show':''}>
                <span  onClick={modalPopUp.bind(this,'credit',false)} className={openAddAmountModal?'modal-overlay show':'modal-overlay'}></span>
                <div className="modal-dialog modal-dialog-sm">
                    <span href="" onClick={modalPopUp.bind(this,'credit',false)} className="modal-close">&times;</span>
                    <h5>Credit Amount</h5>
                    <div className="modal-content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group">
                                    <input type="number" className="form-control mobile-text" placeholder="Amount in USD"  value={accountAddAmount} onChange={e=>(setAccountAddAmount(e.target.value),setAccountAddAmountINR(e.target.value*roe))} />
                                </div>
                                <div className="form-group">
                                    <input type="number" className="form-control mobile-text" value={accountAddAmountINR}  placeholder="Amount in INR" disabled />
                                </div>
                                <div className="form-group" value={paymentMethod} onChange={e=>(setpaymentMethod(e.target.value),setCryptoCoin(""))}>
                                    <select className="form-control payment-mode">
                                        <option value="CASH_IN_HAND">Cash in Hand</option>
                                        <option value="UPI">UPI</option>
                                        <option value="CARD">Card</option>
                                        <option value="CRYPTO">Crypto</option>
                                    </select>
                                </div>
                                {
                                    (paymentMethod === 'CRYPTO') &&(
                                        <div className="form-group" value={paymentMethod} onChange={e=>(setCryptoCoin(e.target.value))}>
                                            <select className="form-control payment-mode">
                                                <option value="Bitcoin">Bitcoin BTC</option>
                                                <option value="Ethereum">Ethereum ETH</option>
                                                <option value="Dogecoin">Dogecoin DOGE</option>
                                                <option value="SHIBA">SHIBA SHIB</option>
                                                <option value="Tether">Tether USDT</option>
                                                <option value="TRON">TRON TRX</option>
                                            </select>
                                        </div>
                                        )
                                }
                                <div className="form-group" style={{textAlign: 'center', paddingTop: '17px'}}>
                                    <button className="period" onClick={addAmount}>Credit Amount</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            

        </div>

    )
}

export default Account;